import React, { PureComponent, Suspense } from "react";
import { CloudUrl } from "../../../constants";
const OnsiteUploadButton = React.lazy(() => import("../UserComponents/OnsiteUploadButton"));
const PlanLimit = React.lazy(() => import("./Errors/PlanLimit"));
const VisitorLimit = React.lazy(() => import("./Errors/VisitorLimit"));
const ErrorMessage = React.lazy(() => import('./Errors/errorMessage'));
const Error201 = React.lazy(() => import("./ErrorCode/201"));
const Error202 = React.lazy(() => import("./ErrorCode/202"));
const Error203 = React.lazy(() => import("./ErrorCode/203"));
const Error204 = React.lazy(() => import("./ErrorCode/204"));
const Error205 = React.lazy(() => import("./ErrorCode/205"));
const Error206 = React.lazy(() => import("./ErrorCode/206"));
const Error207 = React.lazy(() => import("./ErrorCode/207"));
const Error208 = React.lazy(() => import("./ErrorCode/208"));
const Error209 = React.lazy(() => import("./ErrorCode/209"));
const Error211 = React.lazy(() => import("./ErrorCode/211"));
const Error212 = React.lazy(() => import("./ErrorCode/212"));

const ErrorType = React.memo(({ error, wall }) => {
  let editor = window.editor ? true : window.location.pathname.includes('/editor');
  if (error.errorWithMessage) return error.errorWithMessage && Object.keys(error.errorWithMessage).length > 0 ? error.errorWithMessage.type == "VISITOR_LIMIT_EXCEEDED" ? <VisitorLimit error={error} /> : <ErrorMessage error={error} /> : null
  else if (error.planLimit) return <PlanLimit />
  else if (error.themeError && Object.keys(error.themeError).length) {
    switch (error.themeError.error_code) {
      case 201:
        return <Suspense fallback={<></>}><Error201 {...error.themeError} wall={wall} editor={editor} /></Suspense>;
      case 202:
        return <Suspense fallback={<></>}><Error202 {...error.themeError} wall={wall} editor={editor} /></Suspense>;
      case 203:
        return <Suspense fallback={<></>}><Error203 {...error.themeError} wall={wall} editor={editor} /></Suspense>;
      case 204:
        return <Suspense fallback={<></>}><Error204 {...error.themeError} wall={wall} editor={editor} /></Suspense>;
      case 205:
        return <Suspense fallback={<></>}><Error205 {...error.themeError} wall={wall} editor={editor} /></Suspense>;
      case 206:
        return <Suspense fallback={<></>}><Error206 {...error.themeError} wall={wall} editor={editor} /></Suspense>;
      case 207:
        return <Suspense fallback={<></>}><Error207 {...error.themeError} wall={wall} editor={editor} /></Suspense>;
      case 208:
        return <Suspense fallback={<></>}><Error208 {...error.themeError} wall={wall} editor={editor} /></Suspense>;
      case 209:
        return <Suspense fallback={<></>}><Error209 {...error.themeError} wall={wall} editor={editor} /></Suspense>;
      case 211:
        return <Suspense fallback={<></>}><Error211 {...error.themeError} wall={wall} editor={editor} /></Suspense>;
      case 212:
        return <Suspense fallback={<></>}><Error212 {...error.themeError} wall={wall} editor={editor} /></Suspense>;
      default:
        return ""
    }

  } else return error.errorWithMessage && Object.keys(error.errorWithMessage).length > 0 ? <ErrorMessage error={error} /> : null
})
export default class ErrorPage extends PureComponent {
  render() {
    const ErrorImage = { backgroundImage: `url(${CloudUrl}/media/images/nopost.png)` }
    const { error, wall } = this.props
    const queryParams = new URLSearchParams(window.location.search);
    const previewId = queryParams.get('preview');
    return (
      <>
        {previewId ? document.body.classList.add(`tb_previw-error`) : null}
        <div className={`tb_error_page tb_error_theme_${(wall != null && wall) ? wall.Personalization.widgetTheme : 'wall_error'}`}>
          <div className="tb_error_page_bg" style={ErrorImage}> </div>
          {(wall && Object.keys(wall).length > 0 && wall.UserRule.on_site_upload && wall.UgcSettings.onsite_status) ? <div className="tb_error_onsite_btn">
            <Suspense fallback={<></>}><OnsiteUploadButton UgcSettings={wall.UgcSettings} id={wall.Wall.id} /></Suspense>
          </div> : null}
          <ErrorType error={error} wall={wall} />
          {/* {errorType(error, wall)} */}
        </div>
      </>
    );
  }
}
